$cell-size: 100px;
$gap: 1px;
$calendar-width: ($cell-size * 7) + ($gap + 6) + 20px;

$margin-monday: 0;
$margin-tuesday: ($cell-size + $gap);
$margin-wednesday: ($cell-size + $gap) * 2;
$margin-thursday: ($cell-size + $gap) * 3;
$margin-friday: ($cell-size + $gap) * 4;
$margin-saturday: ($cell-size + $gap) * 5;
$margin-sunday: ($cell-size + $gap) * 6;

$begining-margin:
  "mon" $margin-monday,
  "tue" $margin-tuesday,
  "wed" $margin-wednesday,
  "thu" $margin-thursday,
  "fri" $margin-friday,
  "sat" $margin-saturday,
  "sun" $margin-sunday;
